	module.exports = {"data":{"post":{"html":"<p><a href=\"https://www.overseasvotefoundation.org/\"><img style=\"float:left\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2010/11/ovf-badge-voted-2010.jpg\" alt=\"ovf-badge-voted-2010.jpg\" border=\"0\" width=\"230\" height=\"230\" /></a>Tomorrow are the mid-term elections in the US, in case you haven't heard. My vote is already in and counted. If you don't know how to vote from overseas, the <a href=\"https://www.overseasvotefoundation.org/\">Overseas Vote Foundation</a> can help you get started. You might still have a chance to get in your vote for tomorrow, or at the very least, be set to go for the next election. The procedure is different from state to state, but they can help you out.</p>","fields":{"title":"I Live Overseas... And I Vote","date":"01 Nov 2010","url":"https://papascott.de/2010-11-01-i-live-overseas-and-i-vote/"}}},"pathContext":{"slug":"/2010-11-01-i-live-overseas-and-i-vote/","prev":{"fields":{"title":"We didn't know anything then","date":"25 Oct 2010","slug":"/2010-10-25-we-didnt-know-anything-then/"}},"next":{"fields":{"title":"Got Your Back","date":"15 Nov 2010","slug":"/2010-11-15-got-your-back/"}}}}

